.section-operaciones{
    border: 2px solid rgb(196, 196, 196);
    height: 480px;
    box-shadow: 0px 0px 5px gray;
    border-radius: 10px;
    
    .menu{
        height: 100%;
        font-size: 1.1rem;
        font-weight: bold;
        width: 100%;
        background-color: #1A1A1A;
        display: flex;
        justify-content: center;
        align-items: center;
        border-top-left-radius: 10px;
        border-bottom-left-radius: 10px;
        

        ul{
            list-style-type:none;
            padding: 0;
            text-align: center;

            li{
                padding: 10px 0;
                margin: 10px 0;
                line-height: normal;
                background-color: #1A1A1A;
                border-bottom-right-radius: 25px;
                border-top-right-radius: 25px;
                border: 1px solid rgb(190, 190, 190);
                box-shadow: 0px 0px 5px rgb(190, 190, 190);
                transition: .4s ease;
            }

            li.alf{
                padding: 50px 0;
            }

            li.seleccionado{
                background-color: #4EC24E;
                color: black;
                transform: translateX(10px);
                transition: .2s ease;
            }

            li.seleccionado:hover{
                transform: translateX(0px);
                background-color: #4EC24E;
            }

            li:hover{
                background-color: white;
                color: black;
                transform: scale(1.03);
                // transform: translateX(10px);
                transition: .4s ease;
                cursor: pointer;
            }

        }

    }

    .workin-space{
        border-left: 5px solid white;
        height: 100%;
        background-color: #1A1A1A;
        border-top-right-radius: 10px;
        border-bottom-right-radius: 10px;
        border-left-style: 1px solid white;

        h3{
            width: 100%;
            text-align: center;
            // margin: 30px 0 0 0;
            color: white;
            padding: 30px 0 0 0;
            font-size: 1.7rem;
        }

        .limpio{
            width: 70%;
            height: 100%;
            margin: 0 auto;
            
            .images{
                width: 100%;
                height: 70%;
                margin: 0 auto;
                padding-top: 50px;
                display: flex;
                justify-content: center;
                align-items: center;
            
                img{
                    height: 150px;
                    width: 150px;
                    margin: 60px 0 0 20px;
                    opacity: .2;
                }

            }

            p{
                font-size: 1.5rem;
                line-height: normal;
                text-align: center;
            }
        }

        .formulario-seleccion-lenguaje{

            width: 100%;
            height: 100%;
            // background-color: red;

            &__form{
                width: 90%;
                height: 60px;
                display: flex;
                align-items: center;
                justify-content: center;
                margin: 20px auto;
                border: 1px solid rgb(73, 196, 73);
                border-radius: 5px;
                label{
                    margin: 10px;
                    color: white;
                    padding: 0;
                }
                select{
                    width: 200px;
                    border-radius: 5px;
                    font-size: 1rem;
                }
                .col{
                    width: 150px;
                    display: flex;
                    padding: 0;
                    justify-content: center;
                    input{
                        border-radius: 5px;
                        width: 60%;
                        margin: 0;
                    }
                }
                .boton{
                    background-color: #45AC4E;
                    border-radius: 5px;
                    height: 48px;
                    font-size: 1rem;
                }

                .boton:hover{
                    background-color: #54db54;
                }

            }

            .salida{
                background-color: white;
                height: 60%;
                border-radius: 5px;
                display: flex;
                font-size: 1.5rem;
                overflow: auto;
                text-align: center;
                justify-content: center;
                align-items: center;
                p.pot{
                    width: 100%;
                    height: 100%;
                    padding-top: 10px;
                    font-size: .8rem;
                }
            }

        }

        .formato-seleccion-lenguajes{
            width: 100%;
            height: 100%;
            // background-color: red;

            .row-1{
                width: 40%;
                margin: 0 auto;
                border: 1px solid rgb(73, 196, 73);
                border-radius: 5px;
                padding: 10px;
                label{
                    text-align: center;
                }
                select{
                    border-radius: 5px;
                    font-size: 1rem;
                }
            }

            .row-2{
                width: 96%;
                margin: 10px auto;
                border: 1px solid rgb(73, 196, 73);
                padding: 10px;
                border-radius: 5px;
                
                select{
                    border-radius: 5px;
                    font-size: 1rem;
                }

            }

            .row-unica{
                width: 96%;
                margin: 20px auto;
                border: 1px solid rgb(73, 196, 73);
                padding: 10px;
                border-radius: 5px;
                
                select{
                    border-radius: 5px;
                    font-size: 1rem;
                }
            }

            .boton{
                margin-top: 10px;
                border-radius: 5px;
                font-size: 1rem;
                background-color: rgb(73, 196, 73);
            }

            .Salida{
                width: 96%;
                margin: 8px auto;
                background-color: white;
                height: 25%;
                border-radius: 5px;
                display: flex;
                text-align: center;
                align-items: center;
                justify-content: center;
                font-size: 1rem;
                overflow: auto;
                p{  
                    width: 100%;
                    height: 100%;
                    padding-top: 10px;
                    height: 100%;
                    font-size: 1.5rem;
                }
            }

            .Salida-unica{
                width: 96%;
                margin: 20px auto;
                background-color: white;
                height: 40%;
                border-radius: 5px;
                display: flex;
                text-align: center;
                align-items: center;
                justify-content: center;
                font-size: 1rem;
                overflow: auto;
                p{
                    width: 100%;
                    padding-top: 10px;
                    height: 100%;
                    font-size: 1.5rem;
                }
            }

        }

    }

}